// MatchEvents.tsx
import { 
  Timeline, 
  TimelineItem, 
  TimelineSeparator, 
  TimelineDot, 
  TimelineContent, 
  TimelineConnector 
} from '@mui/lab';
import { Typography } from '@mui/material';

interface MatchEvent {
  time: string;
  title: string;
  description: string;
  hashtags: string[];
}

interface Props {
  events: MatchEvent[];
}

const MatchEvents = ({ events }: Props) => {
  return (
    <Timeline position="alternate">
      {events.map((event, index) => (
        <TimelineItem key={index}>
          <TimelineSeparator>
            <TimelineDot color={event.title.includes('Goal') ? 'success' : 'warning'} />
            {index < events.length - 1 && <TimelineConnector />}
          </TimelineSeparator>
          <TimelineContent sx={{ py: '12px', px: 2 }}>
            <Typography variant="h6" component="span">
              {event.time} - {event.title}
            </Typography>
            <Typography variant="body2" color="text.secondary">
              {event.description}
            </Typography>
            <div style={{ marginTop: 8 }}>
              {event.hashtags.map((tag, tagIndex) => (
                <span 
                  key={tagIndex}
                  style={{
                    backgroundColor: '#1976d2',
                    color: 'white',
                    borderRadius: 4,
                    padding: '2px 8px',
                    marginRight: 8,
                    fontSize: '0.8rem'
                  }}
                >
                  {tag}
                </span>
              ))}
            </div>
          </TimelineContent>
        </TimelineItem>
      ))}
    </Timeline>
  );
};

export default MatchEvents;