// App.jsx
import { useState, useEffect } from 'react';
import { Grid, Alert, AlertTitle } from '@mui/material';
import SentimentTimeline from './components/SentimentTimeline';
import MatchEvents from './components/MatchEvents';
import TweetVolumeChart from './components/TweetVolumeChart';

const App = () => {
  const [sentimentData, setSentimentData] = useState([]);
  const [matchEvents, setMatchEvents] = useState([]);
  const [alerts, setAlerts] = useState([]);
  const [connectionStatus, setConnectionStatus] = useState('connecting');

  useEffect(() => {
    const ws = new WebSocket('ws://localhost:3000');

    ws.onopen = () => {
      setConnectionStatus('connected');
      console.log('WebSocket connected');
    };

    ws.onmessage = (event) => {
      try {
        const message = JSON.parse(event.data);
        
        switch(message.type) {
          case 'sentiment':
            setSentimentData(prev => [...prev, message.data]);
            break;
          case 'event':
            setMatchEvents(prev => [...prev, message.data]);
            break;
          case 'alert':
            setAlerts(prev => [{
              ...message.data,
              id: Date.now().toString()
            }, ...prev.slice(0, 4)]);
            break;
          default:
            console.warn('Received unknown message type:', message);
        }
      } catch (error) {
        console.error('Error parsing WebSocket message:', error);
      }
    };

    ws.onerror = (error) => {
      console.error('WebSocket error:', error);
      setConnectionStatus('disconnected');
    };

    ws.onclose = () => {
      setConnectionStatus('disconnected');
      console.log('WebSocket disconnected');
    };

    return () => {
      ws.close();
    };
  }, []);

  return (
    <div className="App" style={{ padding: '20px' }}>
      <h1 style={{ marginBottom: '20px' }}>
        Real Madrid vs Barcelona - Live Sentiment Dashboard
        <span style={{
          display: 'inline-block',
          marginLeft: '10px',
          width: '12px',
          height: '12px',
          borderRadius: '50%',
          backgroundColor: connectionStatus === 'connected' ? '#4CAF50' : 
                         connectionStatus === 'connecting' ? '#FFC107' : '#F44336'
        }} title={connectionStatus}></span>
      </h1>

      {/* Alerts Section */}
      <div style={{ marginBottom: '20px' }}>
        {alerts.map((alert) => (
          <Alert 
            key={alert.id}
            severity={alert.severity}
            sx={{ mb: 2 }}
            onClose={() => setAlerts(prev => prev.filter(a => a.id !== alert.id))}
          >
            <AlertTitle>{alert.title}</AlertTitle>
            {alert.message}
          </Alert>
        ))}
      </div>

      {/* Dashboard Grid */}
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <SentimentTimeline data={sentimentData} />
        </Grid>
        <Grid item xs={12} md={4}>
          <MatchEvents events={matchEvents} />
        </Grid>
        <Grid item xs={12}>
          <TweetVolumeChart data={sentimentData} />
        </Grid>
      </Grid>
    </div>
  );
};

export default App;