// TweetVolumeChart.tsx
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

interface SentimentData {
  timestamp: string;
  positive: number;
  neutral: number;
  negative: number;
}

interface Props {
  data: SentimentData[];
}

const TweetVolumeChart = ({ data }: Props) => {
  return (
    <div style={{ height: 300 }}>
      <ResponsiveContainer width="100%" height="100%">
        <BarChart
          data={data}
          margin={{ top: 20, right: 30, left: 20, bottom: 20 }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis 
            dataKey="timestamp"
            tickFormatter={(time) => new Date(time).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
          />
          <YAxis />
          <Tooltip 
            formatter={(value: number) => [`${value} tweets`, value > 1 ? 'Total tweets' : 'Tweet']}
            labelFormatter={(label) => `Time: ${new Date(label).toLocaleTimeString()}`}
          />
          <Legend />
          <Bar 
            dataKey="positive" 
            name="Positive Tweets" 
            stackId="a" 
            fill="#82ca9d" 
            radius={[4, 4, 0, 0]} 
          />
          <Bar 
            dataKey="neutral" 
            name="Neutral Tweets" 
            stackId="a" 
            fill="#8884d8" 
            radius={[4, 4, 0, 0]} 
          />
          <Bar 
            dataKey="negative" 
            name="Negative Tweets" 
            stackId="a" 
            fill="#ff7300" 
            radius={[4, 4, 0, 0]} 
          />
        </BarChart>
      </ResponsiveContainer>
    </div>
  );
};

export default TweetVolumeChart;